.icon {
	display: inline-block;
	width: 1em;
	height: 1em;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
	&_size_large {
		font-size: 100px;
	}
	&_size_services {
		font-size: 75px;
	}
	&_color_yellow {
		color: #e5c90f;
	}
	&_angle {
		position: relative;
		width: 22px;
		height: 41px;

		background-image: url('../images/icon-angle.png');
		background-repeat: no-repeat;
		background-position: 50% 50%;
		background-size: contain;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			background-image: url('../images/icon-angle-yellow.png');
			background-repeat: no-repeat;
			background-position: 50% 50%;
			background-size: contain;
			opacity: 0;
			visibility: hidden;
		}
		&:hover, &:focus {
			&:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
	&_angle_yellow {
		background-image: url('../images/icon-angle-yellow.png');
	}
	&_angle_reverse {
		transform: rotate(-180deg);
	}
}


