.slider {
	position: relative;
	@include lg() {
		padding-left: 30px;
		padding-right: 30px;
	}
	@include xl() {
		padding-left: 60px;
		padding-right: 60px;
	}
	.owl-carousel {
		position: static;
	}
	.owl-nav {
		position: absolute;
		bottom: 10px;
		left: 0;
		right: 0;
		@include md() {
			bottom: auto;
			top: 50%;
			left: 15px;
			right: 15px;
			transform: translate(0, -50%);
			margin-top: -20px;
		}
		[class*="owl-"] {
			&:hover {
				background-color: transparent;
			}
		}
		.owl-prev, .owl-next {
			position: absolute;
			top: 0;
			left: 50%;
			@include md() {
				left: auto;
			}
		}
		.owl-prev {
			// left: 15px;
			transform: translate(-50%, 0);
			margin-left: -20px;
			@include md() {
				left: -20px;
				margin: 0;
				transform: translate(0, 0);
			}
			@include lg() {
				left: -15px;
			}
			@include xl() {
				left: -5px;
			}
		}
		.owl-next {
			left: auto;
			right: 50%;
			transform: translate(50%, 0);
			margin-right: -20px;
			// right: 15px;
			@include md() {
				right: -20px;
				margin: 0;
				transform: translate(0, 0);
			}
			@include lg() {
				right: -15px;
				
			}
			@include xl() {
				right: -5px;
			}
		}
		.icon {
			@include mmd() {
				width: 16px;
				height: 30px;
			}
		}
	}
	.owl-dots {
		margin-top: 30px;
		.owl-dot {
			span {
				margin: 5px 5px 5px 4px;
				width: 40px;
				height: 9px;
				background-color: #fff;
				border-radius: 0;
			}
			&:not(.active) {
				&:hover {
					span {
						background-color: #383b45;
					}
				}
			}
			&.active {
				span {
					background-color: #fdb900;
				}
			}
		}
	}
	&__flex {

	}
	&__col {
		position: static;
	}
	&__owl {
		position: static;
	}
	&__testimonial {
		// position: static;
	}
}