// fonts
// ================================
// ================================

@mixin fz($v-min, $v-max, $w-min, $w-max, $fallback: false) {
  
  font-size: $v-min;
  
  @media (min-width: $w-min) {
	@if ($fallback) {
	  font-size: $fallback;
	}
	font-size: calcValues($v-min, $v-max, $w-min, $w-max, px);
  }
  @media (min-width: $w-max) {
	font-size: $v-max;
  }
}

