// placeholder
// ===============


@mixin iph() {
	&.placeholder {
		@content;
	}
	&:-moz-placeholder {
		@content;
	}
	&::-moz-placeholder {
		@content;
	}
	&:-ms-input-placeholder {
		@content;
	}
	&::-webkit-input-placeholder {
		@content;
	}
}

// =============================================
// =============================================





@mixin lcb() {
	& > * {
		@content;
		&:last-child {
			margin-bottom: 0;
		}
	}
}

