.dropdown {
	&_lang {
		.dropdown__button {
			padding: 5px 8px 5px 10px;

			color: #fff;
			font-weight: 600;
			text-transform: uppercase;
			@include fz(16px, 22px, $mixin-min-viewport, $mixin-max-viewport, 22px);

			background-color: rgba(#000, .6);
			border-radius: 4px;
			border-color: transparent;
			&:after {
				margin-left: 11px;
			}
		}
		.dropdown__menu {

		}
	}
}
.dropdown__button {
	margin: 5px 0;

	&:after {
		position: relative;
		top: 2px;
	}
	&_carret_none {
		&:after {
			display: none;
		}
	}
}
.dropdown__menu {
	padding: 0;
	border: none;
	border-radius: 5px;
	display: block;
	margin-top: -10px;
	min-width: 100px;

	opacity: 0;
	visibility: hidden;
	// z-index: 1;

	background-color: rgba(0, 0, 0, 0.6);
	border-radius: 4px;
	
	&.show {
		margin: 2px 0;

		opacity: 1;
		visibility: visible;
		transition: opacity ease .2s,
					visibility ease .2s,
					margin ease .2s;
	}
	&_width_auto {
		min-width: auto;
		width: auto;
	}
}

.dropdown__link {
	color: #fff;
	border-radius: 0;
	padding-left: 10px;
	padding-right: 10px;
	&:hover {
		background-color: transparent;
		color: #fff;
	}
}