html {

	font-size: $rem-font-size;
}
body {
	position: relative;

	font-family: $document-font-family;
	font-weight: $document-font-weight;
	font-size: $document-font-size;
	line-height: $document-line-height;
	color: $document-color;

	background-color: $document-background;
	background-image: url('../images/main-bg.jpg');
	background-position: 0 0;
	background-repeat: no-repeat;
	background-attachment: fixed;
	@media only screen and (min-width: 1920px) {
		background-size: cover;
	}
}

img {
	max-width: 100%;
	height: auto;
}

button.iconSvg {
	border: none;
	padding: 0;

	background-color: transparent;
}
input, textarea {
	&:focus {
		outline: none;
	}
}
button, input[type="submit"] {
	cursor: pointer;
	&:focus {
		outline: none;
	}
}
.iconSvg {
	display: inline-block;
}
a.iconSvg {
	&:hover {
		text-decoration: none;
	}
}
a {
	text-decoration: none;
	color: #000;
	&:hover {
		color: #000;
	}
	&:focus {
		outline: none;
	}
}

.pixelperfect {
	@include xl() {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 9999;

		background: url("../images/HC rastr.jpg") 0 0 no-repeat;

		opacity: .3;
	}
	display: none;
}


.container-fluid {
	max-width: 1950px;
	margin-left: auto;
	margin-right: auto;
}
[class*="col-"] {
	margin-bottom: 15px;
	margin-top: 15px;
}


.padding {
	padding-left: 15px;
	padding-right: 15px;
}


.table-responsive {
	&:only-child {
		margin-bottom: 0;
	}
}


.child-margin-medium {
	& > * {
		@include mb(10px, 15px, $mixin-min-viewport, $mixin-max-viewport, 15px);
		@include mt(10px, 15px, $mixin-min-viewport, $mixin-max-viewport, 15px);
	}
}

.child-margin-little {
	& > * {
		margin: 8px 0;
		
	}
}

.margin-first-last-null {
	& > * {
		&:first-child {
			margin-top: 0;
		}
		&:last-child {
			margin-bottom: 0;
		}
	}
}

