.front {
	position: relative;
	padding: 66px 0;
	@include mmd() {
		padding: 30px 0;
	}
}
.front__dropdown {
	position: absolute;
	top: 17px;
	right: 22px;
	@include mmd() {
		top: 0;
		right: 10px;
	}
}
.front__logo {
	@include mmd() {
		max-width: 150px;
	}
}
.front__donate-button {
	width: 100%;
	max-width: 290px;
}

.front__slider {
	margin-bottom: 50px;
	@include md() {
		margin-bottom: 0;
	}
	// @include mt(35px, 75px, $mixin-min-viewport, $mixin-max-viewport, 75px);
	// @include mb(25px, 65px, $mixin-min-viewport, $mixin-max-viewport, 65px);
}